AoI;YAApI,oIAAoI;CACvI;AACD;IACI,kJAA0I;YAA1I,0IAA0I;CAC7I;AACD;IACI,6IAAqI;YAArI,qIAAqI;CACxI;CACA;;AAED,iBAAiB;AAEjB;IACI,oBAAoB;IACpB,eAAe;CAClB;AC8MD;EACA,WAAA;CACA","file":"BuyerRegister.vue","sourcesContent":["/* ---------- Global Styling ---------- */\n\nbody {\n    font: 18px 'Roboto';\n    background: linear-gradient(to bottom right, #FFFFFF 0%,  #D8D4DC 100%);\n    background-size: cover;\n    text-align:center;\n    padding-top: 30px;\n    min-height: 100vh; \n}\n\n#login a, p {\n    text-align:center;\n    font: 14px 'Roboto';\n    color: #29105b;\n}\n\n#login h1 {\n    font: 28px 'Roboto';\n    color: #29105b;\n    text-align:center;\n}\n\n#login h2 {\n    color: #29105b;\n    text-align:center;\n}\n\n#form-footer{\n    padding-bottom:5px;\n    padding-top:15px;\n    text-align: center;\n}\n#form-footer p {\n    text-align: center;\n    font: 12px 'Roboto';\n    color: rgb(129, 114, 114);\n}\n\n\n/* Card Formatting */\n\n.md-card {\n    margin-bottom: 0px;\n    max-width:700px;\n    margin-left: auto;\n    margin-right:auto;\n    border-radius: 10px;\n    background-color:white;\n}\n\n/* .card #rounded {\n    border-radius: 10px;\n} */\n\n.md-steppers {\n    color: purple;\n}\n\n/* Container Formatting */\n\n.container#center {\n    /* margin-left: 0 auto;  */\n    /* shorthand for:\n    margin-top: 0;\n    margin-bottom: 0;\n    margin-left: auto;\n    margin-right: auto; */\n    text-align:center;\n}\n\n.container p {\n    text-align: center;\n    font: 12px 'Roboto';\n    color: rgb(129, 114, 114);\n}\n\n/* --------- Register Form---------- */\n\nform {\n    border-radius: 5px;\n    max-width:800px;\n    width:100%;\n    /* margin: 5% auto; */\n    padding: 5px 10px;\n    background-color: #FFFFFF;\n    overflow: hidden; \n    border-radius: 25px;\n}\n\n.center {\n    display: block;\n    margin-left: auto;\n    margin-right: auto;\n    width: 100%;\n    height:auto;\n    animation-name: moveInLeft;\n    animation-duration: 2s;\n}\n\n#register {\n    border-radius: 10px;\n}\n\n#register, label{\n    font: 15px 'Roboto';\n    color: rgb(94, 94, 94);\n    text-align:left;\n}\n\n#personalinfo {\n    /* width: 49.5%;  */\n    float:left;\n    border-right: 1px dotted #CCC;\n    box-sizing: border-box;\n    padding: 0px 15px 0px 0px;\n    overflow: hidden;\n    clear: both;\n}\n  \n#companyinfo {\n    /* width:49.5%; */\n    float:right;\n    box-sizing: border-box;\n    padding: 0px 0px 0px 15px;\n    overflow: hidden;\n    clear: both;\n}\n\n#register-form-footer{\n    border-top: 2px #ac90be;\n}\n\n\n.fixed-horizontal-center {\n    display: block;\n    margin-left: auto;\n    margin-right: auto;\n    width: 800px;\n}\n\n.error {\n    color: red;\n    font-size: 15px;\n}\n\n#sign-up-link{\n    color: #850696;\n}\n#sign-up-link:hover, \n#sign-up-link:active, \n#sign-up-link:focus {\n    color: #b109c7;\n}\n\np {\n    text-align: left;\n    color: white;\n    /* font-size: 15px */\n    font: 20px \"roboto\";\n}\n\n#btn-login {\n    color: white;\n    border-color: #850696;\n    background-color: #850696;\n    justify-content: center;\n    display: inline-block;\n    border-radius: 20px;\n}\n\n#btn-login:hover, \n#btn-login:active, \n#btn-login:focus {\n    color: white;\n    border-color: #9c07af;\n    background-color: #9c07af;\n    justify-content: center;\n    display: inline-block;\n    border-radius: 20px;\n}\n\n#btn-signup {\n    display: inline-block;\n    border-radius: 5px;\n    height:40px;\n    width:200px;\n    border-color:#29105b;\n}\n\n#btn-submit:hover {\n    color: white;\n    border-color: #850696;\n    background-color: #850696;\n    justify-content: center;\n    display: inline-block;\n    border-radius: 5px;\n}\n\n#btn-next {\n    color: white;\n    border-color: #850696;\n    background-color: #850696;\n    justify-content: center;\n    display: inline-block;\n    border-radius: 5px;\n}\n#btn-next:hover {\n    color: white;\n    border-color: #b109c7;\n    background-color: #b109c7;\n    justify-content: center;\n    display: inline-block;\n    border-radius: 5px;\n}\n\n#btn-previous {\n    color: #850696;\n    border-color: #850696;\n    background-color: white;\n    justify-content: center;\n    display: inline-block;\n    border-radius: 5px;\n}\n\n#btn-previous:hover {\n    color: #a109c7;\n    border-color: #a109c7;\n    background-color: rgb(228, 228, 228);\n    justify-content: center;\n    display: inline-block;\n    border-radius: 5px;\n}\n\n\n\n/* Responsive ish */\n\n.responsive-component.small .fixed-horizontal-center {\n    display: block;\n    margin-left: auto;\n    margin-right: auto;\n    width: 475px;  \n}\n\n\n/* ------Login Form--------- */\n\n#login {\n    display: block;\n    margin-left: auto;\n    margin-right: auto;\n    width: 350px;  \n}\n\n#login_form-footer{\n    padding-bottom:5px;\n    padding-top:15px;\n    text-align: center;\n    width: 350px;\n}\n\n#login h1 {\n    text-align: center;\n    text-transform: uppercase;\n}\n\n\n\n.login #login label{\n    font: 18px 'Roboto';\n    color: rgb(94, 94, 94);\n    text-align:left;\n}\n\n.login #login h1 {\n    font: 28px 'Roboto';\n    color: #29105b;\n}\np {\n    font-size: 10px;\n    text-align: left;\n}\n\n\n\n\n#legal-links {\n    letter-spacing: 1px;\n    font: 15px 'Roboto';\n    text-align: center;\n    /* color: #917e9e; */\n    color: #29105b;\n}\n\n\n\n#login-logo {\n    display: block;\n    margin-left: auto;\n    margin-right: auto;\n    width: 175px;\n    height:150px;\n}\n\n/* loading animation - replace filled form with loading animation*/\n.loader {\n    color: #3c1760;\n    font-size: 20px;\n    margin: 100px auto;\n    width: 1em;\n    height: 1em;\n    border-radius: 50%;\n    position: relative;\n    text-indent: -9999em;\n    -webkit-animation: load4 1.3s infinite linear;\n    animation: load4 1.3s infinite linear;\n    -webkit-transform: translateZ(0);\n    -ms-transform: translateZ(0);\n    transform: translateZ(0);\n}\n\n@-webkit-keyframes load4 {\n0%,\n100% {\n    box-shadow: 0 -3em 0 0.2em, 2em -2em 0 0em, 3em 0 0 -1em, 2em 2em 0 -1em, 0 3em 0 -1em, -2em 2em 0 -1em, -3em 0 0 -1em, -2em -2em 0 0;\n}\n12.5% {\n    box-shadow: 0 -3em 0 0, 2em -2em 0 0.2em, 3em 0 0 0, 2em 2em 0 -1em, 0 3em 0 -1em, -2em 2em 0 -1em, -3em 0 0 -1em, -2em -2em 0 -1em;\n}\n25% {\n    box-shadow: 0 -3em 0 -0.5em, 2em -2em 0 0, 3em 0 0 0.2em, 2em 2em 0 0, 0 3em 0 -1em, -2em 2em 0 -1em, -3em 0 0 -1em, -2em -2em 0 -1em;\n}\n37.5% {\n    box-shadow: 0 -3em 0 -1em, 2em -2em 0 -1em, 3em 0em 0 0, 2em 2em 0 0.2em, 0 3em 0 0em, -2em 2em 0 -1em, -3em 0em 0 -1em, -2em -2em 0 -1em;\n}\n50% {\n    box-shadow: 0 -3em 0 -1em, 2em -2em 0 -1em, 3em 0 0 -1em, 2em 2em 0 0em, 0 3em 0 0.2em, -2em 2em 0 0, -3em 0em 0 -1em, -2em -2em 0 -1em;\n}\n62.5% {\n    box-shadow: 0 -3em 0 -1em, 2em -2em 0 -1em, 3em 0 0 -1em, 2em 2em 0 -1em, 0 3em 0 0, -2em 2em 0 0.2em, -3em 0 0 0, -2em -2em 0 -1em;\n}\n75% {\n    box-shadow: 0em -3em 0 -1em, 2em -2em 0 -1em, 3em 0em 0 -1em, 2em 2em 0 -1em, 0 3em 0 -1em, -2em 2em 0 0, -3em 0em 0 0.2em, -2em -2em 0 0;\n}\n87.5% {\n    box-shadow: 0em -3em 0 0, 2em -2em 0 -1em, 3em 0 0 -1em, 2em 2em 0 -1em, 0 3em 0 -1em, -2em 2em 0 0, -3em 0em 0 0, -2em -2em 0 0.2em;\n}\n}\n@keyframes load4 {\n0%,\n100% {\n    box-shadow: 0 -3em 0 0.2em, 2em -2em 0 0em, 3em 0 0 -1em, 2em 2em 0 -1em, 0 3em 0 -1em, -2em 2em 0 -1em, -3em 0 0 -1em, -2em -2em 0 0;\n}\n12.5% {\n    box-shadow: 0 -3em 0 0, 2em -2em 0 0.2em, 3em 0 0 0, 2em 2em 0 -1em, 0 3em 0 -1em, -2em 2em 0 -1em, -3em 0 0 -1em, -2em -2em 0 -1em;\n}\n25% {\n    box-shadow: 0 -3em 0 -0.5em, 2em -2em 0 0, 3em 0 0 0.2em, 2em 2em 0 0, 0 3em 0 -1em, -2em 2em 0 -1em, -3em 0 0 -1em, -2em -2em 0 -1em;\n}\n37.5% {\n    box-shadow: 0 -3em 0 -1em, 2em -2em 0 -1em, 3em 0em 0 0, 2em 2em 0 0.2em, 0 3em 0 0em, -2em 2em 0 -1em, -3em 0em 0 -1em, -2em -2em 0 -1em;\n}\n50% {\n    box-shadow: 0 -3em 0 -1em, 2em -2em 0 -1em, 3em 0 0 -1em, 2em 2em 0 0em, 0 3em 0 0.2em, -2em 2em 0 0, -3em 0em 0 -1em, -2em -2em 0 -1em;\n}\n62.5% {\n    box-shadow: 0 -3em 0 -1em, 2em -2em 0 -1em, 3em 0 0 -1em, 2em 2em 0 -1em, 0 3em 0 0, -2em 2em 0 0.2em, -3em 0 0 0, -2em -2em 0 -1em;\n}\n75% {\n    box-shadow: 0em -3em 0 -1em, 2em -2em 0 -1em, 3em 0em 0 -1em, 2em 2em 0 -1em, 0 3em 0 -1em, -2em 2em 0 0, -3em 0em 0 0.2em, -2em -2em 0 0;\n}\n87.5% {\n    box-shadow: 0em -3em 0 0, 2em -2em 0 -1em, 3em 0 0 -1em, 2em 2em 0 -1em, 0 3em 0 -1em, -2em 2em 0 0, -3em 0em 0 0, -2em -2em 0 0.2em;\n}\n}\n\n/* Buyer Login */\n\n.login #buyer-login h1 {\n    font: 28px 'Roboto';\n    color: #29105b;\n}\n\n","<template>\n<div>\n  <buyer-header></buyer-header>\n  <div class=\"responsive-component\" v-responsive=\"{\n      small: el => el.width < 860\n      }\">\n    <body>\n      <div class=\"fixed-horizontal-center\">\n        <!-- Loadng animation -->\n        <div class=\"loader\" v-if=\"loadingFlag== 2\">Loading...</div>\n\n        <!-- Form -->\n        <md-card id=\"register\">\n          <md-card-header>\n            <h2 style=\"text-align:center; color:#361555;\">Buyer Sign Up</h2>\n          </md-card-header>\n\n          <md-card-content>\n            <div class=\"error\" v-html=\"error\" />\n            <form>\n              <md-steppers :md-active-step.sync=\"active\" md-linear md-vertical>\n                <md-step\n                  id=\"first\"\n                  :md-done.sync=\"first\"\n                  md-label=\"Personal Info\"\n                  md-description=\"Required\"\n                >\n                  <div class=\"form-group row\">\n                    \n                    <label for=\"name\" class=\"col-sm col-form-label\">First Name:</label>\n                    <div class=\"col-sm-12\">\n                      <input\n                        v-model=\"firstName\"\n                        type=\"text\"\n                        class=\"form-control\"\n                        placeholder=\"Enter your first name\"\n                        v-on:keyup.enter=\"setDone('first', 'second')\"\n                      />\n                    </div>\n                  </div>\n                  \n                  <div class=\"form-group row\">\n                    <label for=\"name\" class=\"col-sm col-form-label\">Last Name:</label>\n                    <div class=\"col-sm-12\">\n                      <input\n                        v-model=\"lastName\"\n                        type=\"text\"\n                        class=\"form-control\"\n                        placeholder=\"Enter your last name\"\n                        v-on:keyup.enter=\"setDone('first', 'second')\"\n                      />\n                    </div>\n                  </div>\n\n                  <div class=\"form-group row\">\n                    <label for=\"email\" class=\"col-sm col-form-label\">Email:</label>\n                    <div class=\"col-sm-12\">\n                      <input\n                        v-model=\"email\"\n                        type=\"email\"\n                        class=\"form-control\"\n                        placeholder=\"Enter your email\"\n                        v-on:keyup.enter=\"setDone('first', 'second')\"\n                      />\n                    </div>\n                  </div>\n\n                  <div class=\"form-group row\">\n                    <label for=\"inputPassword\" class=\"col-sm col-form-label\">Password:</label>\n                    <div class=\"col-sm-12\">\n                      <!-- ADD PASSWORD RESTRICTIONS (length, characters, numbers) -->\n                      <input\n                        v-model=\"password\"\n                        type=\"password\"\n                        class=\"form-control\"\n                        placeholder=\"Must be at least 7 characters long with at least one digit.\"\n                        v-on:keyup.enter=\"setDone('first', 'second')\"\n                      />\n                    </div>\n                  </div>\n\n                  <div class=\"form-group row\">\n                    <label for=\"inputPassword\" class=\"col-sm col-form-label\">Confirm Password:</label>\n                    <div class=\"col-sm-12\">\n                      <input\n                        v-model=\"passwordConfirm\"\n                        type=\"password\"\n                        class=\"form-control\"\n                        placeholder=\"Re-enter your password\"\n                        v-on:keyup.enter=\"setDone('first', 'second')\"\n                      />\n                    </div>\n                  </div>\n                  <br>\n                  <md-button\n                    style=\"border-radius:15px;\"\n                    class=\"md-raised md-primary\"\n                    @click=\"setDone('first', 'second')\"\n                  >Continue</md-button>\n                </md-step>\n\n                <md-step\n                  id=\"second\"\n                  :md-done.sync=\"second\"\n                  md-label=\"Organization Info\"\n                  md-description=\"Required\"\n                >\n                  <!-- Org Name -->\n                  <div class=\"form-group row\">\n                    <label for=\"inputCompanyName\" class=\"col-sm col-form-label\">Organization Name:</label>\n                    <div class=\"col-sm-12\">\n                      <input\n                        v-model=\"companyName\"\n                        type=\"text\"\n                        class=\"form-control\"\n                        placeholder=\"Enter the name of your organization\"\n                        v-on:keyup.enter=\"setDone('second', 'third')\"\n                      />\n                    </div>\n                  </div>\n\n                  <!-- Institution Type -->\n                  <!-- TO DO: plug in vmodal for institution type -->\n                  <div class=\"form-group row\">\n                    <label for=\"Institution-type\" class=\"col-sm col-form-label\">Organization type:</label>\n                      <div class=\"col-sm-12\">