-form-label\">Address line 2:</label>\n                      <div class=\"col-sm-12\">\n                        <input\n                          type=\"text\"\n                          class=\"form-control\"\n                          placeholder=\"Address line 1\"\n                          v-on:keyup.enter=\"setDone('second', 'third')\"\n                        />\n                      </div>\n                    </div>\n\n                    <!-- City -->\n                    <div class=\"form-group row\">\n                      <label for=\"inputCity\" class=\"col-sm col-form-label\">City:</label>\n                      <div class=\"col-sm-12\">\n                        <input\n                          v-model=\"address.city\"\n                          type=\"text\"\n                          class=\"form-control\"\n                          placeholder=\"City\"\n                          v-on:keyup.enter=\"setDone('second', 'third')\"\n                        />\n                      </div>\n                    </div>\n\n                    <!-- State -->\n                    <div class=\"form-group row\">\n                      <label\n                        for=\"inputState\"\n                        class=\"col-sm col-form-label\"\n                      >State / Province / Region:</label>\n                      <div class=\"col-sm-12\">\n                        <input\n                          v-model=\"address.state\"\n                          type=\"text\"\n                          class=\"form-control\"\n                          placeholder=\"State / Province / Region\"\n                          v-on:keyup.enter=\"setDone('second', 'third')\"\n                        />\n                      </div>\n                    </div>\n\n                    <!-- Zip -->\n                    <div class=\"form-group row\">\n                      <label for=\"inputZip\" class=\"col-sm col-form-label\">Zip / Postal code:</label>\n                      <di